.recipe-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 2em;
        border-bottom: 1px solid #999;
    }

    .gallery {
        display: grid;
        grid-gap: 15px;
        margin-top: 60px;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(5, auto);

        >* {
            cursor: pointer;
            position: relative;
            width: 260px;
            height: 440px;
            transition: 0.5s;
            box-shadow: 5px 5px 9px 0px rgb(0 0 0 / 10%);
            opacity: 1;
    }

    .recipe-info {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding: 5px;

        margin-top: -8px;

        // .recipe-mini-title {
        //     color: black;
        // }

        .title-and-mini-title {
            display: flex;
            width: 100%;
            flex-direction: column;
            justify-content: space-around;
            align-items: flex-end;
            padding: 6px;

            .recipe-mini-title {}

            p.title {
                // font-size: .65em;
                color: black;
            }

            >* {
                font-weight: bold;
                margin: 0;
                padding: 4px;
                text-align: end;
            }

            .recipe-price {
                display: flex;

                p:first-child {
                    margin-right: 6px;
                }
            }


        }

        .btns {
            display: flex;
            width: 100%;
            padding-bottom: 10px;
            justify-content: space-around;
            transition: 0.5s;

            button {
                display: flex;
                align-items: center;
                justify-content: center;

                padding: 15px 32px;
                width: 75px;
                height: 26px;
                border-radius: 18px;

                background-color: rgb(125, 120, 120);
                cursor: pointer;
                border: none;
                font-size: 12px;
                font-weight: bold;
                transition: 0.5s;
            }

            >* {

                &:hover {
                    transition: 0.5s;
                    opacity: 0.8;

                    button {
                        transition: 0.5s;
                        opacity: 1;

                        &:hover {
                            background-color: rgb(137, 134, 134);
                        }
                    }
                }
            }
        }


    }

    .recipe {
        border-radius: 14px;

        &:hover {

            img {
                transition: 0.7s;
                filter: brightness(0.85);
            }

            .btns {

                button {

                    width: 45%;
                }
            }

        }

        img {
            float: left;
            width: 100%;
            height: 300px;
            object-fit: cover;
            border-radius: 14px;
            transition: 0.7s;
            filter: brightness(0.6);
        }

    }
}
}

@media screen and (min-width: 551px) {
    .recipe-container {
        .gallery {

            .recipe-info {

                .btns {
                    button {
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}

